import { Dispatch } from "redux"
import {BaseResponse} from "../../features/auth/types";
import {setAppErrorAC, setAppLoaderAC} from "../../app/app-reducer";

/**
 *    ,       .
 *
 * @template T  ,   .
 * @param {BaseResponse<T>} data   ,     .
 * @param {Dispatch} dispatch  Redux dispatch,    .
 * @param {boolean} [isShowGlobalError=true] , ,       .
 * @returns    
 */

export const handleServerAppError = <T>(
  data: BaseResponse<T>,
  dispatch: Dispatch
) => {

    if (data.messages.length) {
      dispatch(setAppErrorAC( data.messages[0] ))
    } else {
      dispatch(setAppErrorAC("some error occurred" ))
    }

  dispatch(setAppLoaderAC(false))
}
